body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

img {
  width: 200px;
  height: 200px;
  position: relative;
  overflow: hidden;
  border-radius: 50%;
}

.text-padding {
  padding-right: 10%;
  padding-left: 10%;
}

p.padding {
  padding-right: 15rem;
}

div.center {
  margin: auto;
  display: block;
  text-align: center;
  padding-top: 15%;
}



.size {
  height: 100vh;
  min-height: 100vh;
}

.btn-primary-outline {
  background-color: transparent;
}